<template>
  <section class="dashboard-content-area">

    <TopMenu :tableSpecifications="tableSpecifications" :selectedCountrySpecification="selectedCountrySpecification"
      :groupedFilters="groupedFilters" :table="table" @toggleIconClicked="toggleSideBar" :dataView="dataView"
      @filtersChanged="filtersChanged = !filtersChanged" />
    <notification ref="notification"></notification>
    <ProConfirmationModal ref="confirmationModal" @confirm="serverAction"></ProConfirmationModal>
    <LinkModal ref="LinkModal" @confirmationModal="openConfirmationModal" @removeReceipt="removeReceipt"
      @addNotification="addNotification"> </LinkModal>
    <TableSubMenu :tableSpecifications="tableSpecifications" :selectedCountrySpecification="selectedCountrySpecification"
      :groupedFilters="groupedFilters" :table="table" :selectedRows="selectedRows" @columnsChanged="this.initialiseTable"
      @countryChanged="this.onTableSpecificationChange" @filtersChanged="filtersChanged = !filtersChanged" />
    {{ selectedRows.length }}
    <section class="data-area" :class="{
      loading: dataView[0] === 'loading',
      'display-chart': dataView[0] === 'chart',
    }">
      <div v-show="dataView[0] === 'loading'" class="loader"></div>
      <div v-show="dataView[0] === 'table'" ref="table" id="table"></div>

      <div class="chart">
        <Charts v-if="dataView[0] === 'chart'" :table="table" :selectedCountrySpecification="selectedCountrySpecification"
          :filtersChanged="filtersChanged" :tableSpecifications="tableSpecifications" />
      </div>
    </section>
  </section>
  <CardModal :isActive="activateModal" @closeModal="toggleModal" :record="modalRecord" :groupedFilters="groupedFilters" />
</template>

<script>
import { TabulatorFull as Tabulator } from "tabulator-tables";
import { url } from "../App.vue";
import { DateTime } from "luxon";
import CardModal from "../components/CardModal.vue";
import Notification from '../components/Notification.vue';
import ProConfirmationModal from "../components/ProConfirmationModal.vue";
import LinkModal from "../components/LinkModal.vue";
import TopMenu from "./TopMenu.vue";
import Charts from "./Charts.vue";
import TableSubMenu from "./TableSubMenu.vue";
import _ from "lodash";



export default {
  props: ["tableSpecifications", "data"],
  data() {
    return {
      table: null,
      tableData: [],
      groupedFilters: [],
      selectedRows: [],
      selectedCountrySpecification: {
        tableDefinition: [],
      },
      dataView: ["loading"],
      activateModal: false,
      modalRecord: [],
      filtersChanged: false,
      contentLoaded: false,
      link_button: `
            <div class="tabulator-responsive-collapse-toggle">
              <button class="attachment-button">
                Link
              </button>
            </div>`,
      update_pro_button: `
            <div class="tabulator-responsive-collapse-toggle">
              <button class="attachment-button">
                Update ORD
              </button>
            </div>`
    };
  },
  methods: {
    toggleSideBar,
    retrieveTableData,
    initialiseTable,
    storeFilters,
    retrieveFilters,
    storeUserColumnCustomisations,
    constructLocalStorageID,
    onTableSpecificationChange,
    viewRecordAttachments,
    toggleModal,
    originalSpecificationChanged,
    clearLocalStorage,
    addTableButtons,
    requestData,
    linkOrderFormatter,
    actionFormatter,
    serverAction,
    openModal,
    openConfirmationModal(payload) {
      this.openModal(payload.data, payload.element, payload.action, this.$refs.confirmationModal)
    },
    removeReceipt,
    updatedSelectedRows,
    getCellMenuContext,
    addNotification(payload) {
      console.log(payload)
      let message = payload.message
      let type = payload.type
      let duration = payload.duration
      let actions = payload.actions
      this.$refs.notification.addNotification(message, type, duration, actions)
    }
  },
  components: {
    CardModal,
    TopMenu,
    TableSubMenu,
    Charts,
    Notification,
    ProConfirmationModal,
    LinkModal
  },
  watch: {
    // whenever tableSpecifications changes, this function will run
    tableSpecifications: onTableSpecificationChange,
    selectedCountrySpecification: {
      async handler(newCountrySpecification, oldCountrySpecification) {
        if (
          oldCountrySpecification !== null ||
          oldCountrySpecification.length != 0
        ) {
          this.dataView[0] = "loading";
          // Ensure that the user's changes persist on a prop change
          this.storeUserColumnCustomisations(oldCountrySpecification);
          this.storeFilters(oldCountrySpecification);
          // Set the next page loaded state to not loaded
          this.contentLoaded = false;
        }
        await this.retrieveTableData(newCountrySpecification);
        this.retrieveFilters(newCountrySpecification);
        this.contentLoaded = true;
      },
      // force eager callback execution which would allow the methods invoked to execute using all
      // old component property values when the watched property has been changed.
      immediate: true,
    },
  },
  beforeUnmount() {
    // Ensure that the user's changes persist on change to another component/page such as the summary page
    this.storeUserColumnCustomisations(this.selectedCountrySpecification);
    this.storeFilters(this.selectedCountrySpecification);
  },
  async beforeMount() {
    // Add luxon date library to the Tabulator form via a property to the global object
    window.DateTime = DateTime;
    // Ensure that the user's changes persist on page reload
    window.addEventListener("unload", () => {
      this.storeUserColumnCustomisations(this.selectedCountrySpecification);
      this.storeFilters(this.selectedCountrySpecification);
    });
    // Ensure that default table view loaded on user login is initialised.
    this.onTableSpecificationChange();
  },
};

function onTableSpecificationChange() {
  if (this.tableSpecifications.length === 0) return;
  const originaltableSpecifications = this.tableSpecifications.countries.find(
    (country) => country.active
  );
  // Created a copy of the table specifications so that edits to the object within current page does not affect the server's table definition
  this.selectedCountrySpecification = structuredClone(
    originaltableSpecifications
  );
}

function storeUserColumnCustomisations(specification) {
  if (this.table === null || specification == null || !this.contentLoaded)
    return;
  const userCustomisation = this.table.getColumnLayout();

  // Remove toggle additional info field and get attachments value before storing the column layout
  specification.tableDefinition.pop();
  userCustomisation.pop();

  // If the view has attachments associated with the record, remove the attachment button before storing column specification
  // Only remove attachments column if the table records had the attachment column inserted
  if (specification.attachments) {
    specification.tableDefinition.pop();
    userCustomisation.pop();
  }

  if (specification.actions && specification.actions.length) {
    specification.tableDefinition.pop();
    userCustomisation.pop();
  }

  if (specification.create_order_button) {
    specification.tableDefinition.pop();
    userCustomisation.pop();
  }
  if (specification.update_order_button) {
    specification.tableDefinition.pop();
    userCustomisation.pop();
  }
  const mergedColumnCustomisation = userCustomisation.map((column, index) => {
    return {
      // Merge initial config with user customisations.
      // Prioritise user customisations to overrwrite initial structure
      // However allow the initial structure visibility to persist since it preserves the visibility of fields that are viewed within the collapsed window.
      // The specification.tableDefinition also contains the user's column visibility selection therefore user column visibility will persist.
      // All other config are captured within tabulator column layout object.
      ...specification.tableDefinition[index],
      ...column,
      visible: specification.tableDefinition[index].visible,
    };
  });
  const id = this.constructLocalStorageID("columns", specification);
  localStorage.setItem(id, JSON.stringify(mergedColumnCustomisation));
}

function constructLocalStorageID(type, specification) {
  if (specification == null || specification.length == 0) return;
  if (type === "raw") return `${specification.id}`;
  if (type === "columns") return `tabulator-${specification.id}-columns`;
  if (type === "filters") return `tabulator-${specification.id}-filter`;
  if (type === "initialLayout")
    return `tabulator-${specification.id}-initialLayout`;
  if (type === "sort") return `tabulator-${specification.id}-sort`;
}
function retrieveFilters(selectedCountrySpecification) {
  if (
    this.table == null ||
    selectedCountrySpecification.tableDefinition.length == 0
  )
    return;
  // Clear all applied filters to the table
  this.table.clearFilter();
  // read from local storage (ungrouped)
  const filterID = this.constructLocalStorageID(
    "filters",
    selectedCountrySpecification
  );
  let tableFilters = JSON.parse(localStorage.getItem(filterID));

  // if tableFilters is empty or null use table filters from http response
  if (tableFilters == null || tableFilters.length == 0) {
    tableFilters = selectedCountrySpecification.initialFilter;
  }

  // assign filters to table (ungrouped)
  // Add all filters whose applied === true (applied) to the table
  for (const filter of tableFilters) {
    if (filter[0].applied && filter[0].filterType === "or") {
      this.table.addFilter([filter]);
    } else if (filter[0].applied) {
      this.table.addFilter(filter);
    }
  }
  // group in order to display (grouped)
  this.groupedFilters = tableFilters;
}

export function toggleSideBar() {
  this.$emit("toggleIconClicked");
}

function storeFilters(specification) {
  if (!this.contentLoaded) return;
  // Retrieve filters from table (ungrouped)
  const filtersToBeStored = this.groupedFilters;
  this.groupedFilters = [];
  const filterID = this.constructLocalStorageID("filters", specification);
  // write to local storage (ungrouped)
  localStorage.setItem(filterID, JSON.stringify(filtersToBeStored));
}

async function retrieveTableData(newCountrySpecification) {

  if (
    newCountrySpecification == null ||
    newCountrySpecification.tableDefinition.length == 0
  ) {
    return;
  }
  const tableViewColumnID = this.constructLocalStorageID(
    "columns",
    newCountrySpecification
  );
  // Retrieve stored column data for the selected table view based on the unique ID for that menu item
  const storedColumnLayout = JSON.parse(
    localStorage.getItem(tableViewColumnID)
  );

  // Compare the default column layout with the layout retreived from the server
  // If there is a change, reset the table view using the server specifications.
  const originalSpecificationChangeStatus = this.originalSpecificationChanged(
    newCountrySpecification
  );

  // If their is stored column data and the server json data specification for the table has not changed
  // Use the stored column data
  if (storedColumnLayout != null && !originalSpecificationChangeStatus) {
    this.selectedCountrySpecification.tableDefinition = storedColumnLayout;
  }

  // Append the appropriate table buttons to the table view
  this.addTableButtons(newCountrySpecification);

  // retreive the list of fields to be displayed for the table data from the column specification data
  const filteredRequestedFields = retrieveTableFieldsList(
    newCountrySpecification
  );
  this.selectedRows = []
  await this.requestData(newCountrySpecification, filteredRequestedFields);
}

export function formatURL(url, dataEndpoint, fields) {
  let formattedUrl;
  if (dataEndpoint.includes("?")) {
    formattedUrl = `${url}${dataEndpoint}&fields=${encodeURI(
      JSON.stringify(fields)
    )}`;
  } else {
    formattedUrl = `${url}${dataEndpoint}?fields=${encodeURI(
      JSON.stringify(fields)
    )}`;
  }
  return formattedUrl;
}

async function requestData(newCountrySpecification, fields) {
  const formattedFields = encodeURIComponent(fields);  // URL-encode the 'fields' parameter value
  const formattedUrl = `${url}${newCountrySpecification.dataEndpoint}?fields=${formattedFields}`;
  // const formattedUrl = formatURL(
  //   url,
  //   newCountrySpecification.dataEndpoint,
  //   fields
  // );
  // Send request to the server endpoint
  const response = await fetch(formattedUrl, {
    credentials: "include", // Ensure that the credentials cookie is stored and issued with cross origin requests.
    method: "GET", // POST, PUT, DELETE, etc.
  });
  // Retrieve the response body and assign the response json data to the appropriate component property
  // Then initialise the table with the retrieved data
  const responseBody = await response.json();

  if (response.ok) {
    this.tableData = responseBody;
    this.initialiseTable(newCountrySpecification);
    // Stop loading animation
    this.dataView[0] = "table";
  }
}

function retrieveTableFieldsList(newCountrySpecification) {
  // Store fields to be displayed within table to be sent within the HTTP request
  const requestFields = newCountrySpecification.tableDefinition.map(
    (column) => {
      if ("field" in column && "compute" in column) {
        return !column.compute ? column.field : null;
      }
    }
  );
  // remove null values from requested field list
  return requestFields.filter((field) => {
    if (field) return field;
  });
}

function addTableButtons(newCountrySpecification) {
  // Add attachment button if the table records have attachments
  if (newCountrySpecification.attachments) {
    newCountrySpecification.tableDefinition.push({
      title: "Attachments",
      formatter: getRecordAttachmentsFormatter,
      headerSort: false,
      visible: true,
      responsive: 0,
      minWidth: 50,
      cellClick: this.viewRecordAttachments,
    });
  }

  if (newCountrySpecification.create_order_button || newCountrySpecification.update_order_button) {
    newCountrySpecification.tableDefinition.push({
      title: "Action",
      formatter: this.linkOrderFormatter,
      headerSort: false,
      visible: true,
      responsive: 0,
      width: 150,
    });
  }

  if (newCountrySpecification.actions && newCountrySpecification.actions.length) {
    newCountrySpecification.tableDefinition.push({
      title: "Actions",
      formatter: this.actionFormatter,
      headerSort: false,
      visible: true,
      responsive: 0,
      width: 150,
      contextMenu: this.getCellMenuContext(newCountrySpecification)
    });

    console.log(newCountrySpecification.tableDefinition)
  }



  // Add toggle buttons to table columns
  newCountrySpecification.tableDefinition.push({
    title: "Additional Info",
    formatter: customResponsiveCollapseFormatter,
    headerSort: false,
    visible: true,
    responsive: 0,
    minWidth: 50,
  });
}

function originalSpecificationChanged(newCountrySpecification) {
  // This function will be used to detect any changes within the server json table column definition
  // If changes to exist, clear local storage for affected table and read column specification from server response.
  const storedInitialLayoutID = this.constructLocalStorageID(
    "initialLayout",
    newCountrySpecification
  );
  const storedInitialLayout = JSON.parse(
    localStorage.getItem(storedInitialLayoutID)
  );
  if (storedInitialLayout !== null) {
    if (_.isEqual(storedInitialLayout, newCountrySpecification)) {
      return false;
    }
  }
  this.clearLocalStorage();
  const initialLayout = JSON.stringify(newCountrySpecification);
  localStorage.setItem(storedInitialLayoutID, initialLayout);
  return true;
}

function clearLocalStorage() {
  const tableColumnSpecificationID = this.constructLocalStorageID(
    "columns",
    this.selectedCountrySpecification
  );
  const tableFilterID = this.constructLocalStorageID(
    "filters",
    this.selectedCountrySpecification
  );
  const tableSortID = this.constructLocalStorageID(
    "sort",
    this.selectedCountrySpecification
  );
  localStorage.removeItem(tableColumnSpecificationID);
  localStorage.removeItem(tableFilterID);
  localStorage.removeItem(tableSortID);
}

function getCellMenuContext(newCountrySpecification){
  let cellContextMenu = []
  console.log('here')
  if(newCountrySpecification.actions){
    newCountrySpecification.actions.forEach(action=>{
      let menuitem = 
      {
        label:action.label,
        action: (e, cell) => {
          this[action.name](cell, action)
        }
    }
    cellContextMenu.push(menuitem)
    })
  }
  return cellContextMenu
  
}

function initialiseTable(newCountrySpecification) {
  //create Tabulator on DOM element with id "example-table"
  this.table = new Tabulator(this.$refs.table, {
    minHeight: "100%", // set height of table (in CSS or here), this enables the Virtual DOM and improves render speed dramatically (can be any valid css height value)
    data: this.tableData, //assign data to table
    layout: "fitDataFill", //resize column width to ensure that all displayed columns fit within the width of the table and the data is being correctly displayed
    responsiveLayout: "collapse", //hide columns that dont fit on the table
    history: true, //allow undo and redo actions on the table
    pagination: "local", //paginate the data
    paginationSize: 5, //allow 7 rows per page of data
    paginationCounter: "rows", //display count of paginated rows in footer
    rowHeight: 70, //set rows to 40px height
    movableColumns: true, //allow column order to be changed
    responsiveLayoutCollapseStartOpen: false,
    resizableColumnFit: true,
    autoResize: true, // prevent auto resizing of table
    layoutColumnsOnNewData: true,
    placeholder: "No Data Available", //display message to user on empty table
    persistence: {
      sort: true, //persist sort
    },
    paginationSizeSelector: [10, 25, 50, 100, true], //select list with an "all" option at the end of the list
    persistenceID: this.constructLocalStorageID("raw", newCountrySpecification),
    columns: this.selectedCountrySpecification.tableDefinition,
    initialSort: newCountrySpecification.initialSort,
  });

  // Add an cell edited event to the table which will use to trigger the method to update shipment comment
  this.table.on("cellEdited", updateKPIComment);

  this.table.on("rowSelected", this.updatedSelectedRows)
  this.table.on("rowDeselected", this.updatedSelectedRows)

}

function updatedSelectedRows(row) {
  console.log(row)
  this.selectedRows = this.table.getSelectedRows()

}
function updateKPIComment(cell) {
  const editedRowData = cell.getRow().getData();
  if ("Shipper" in editedRowData) {
    updateShipmentComment(editedRowData);
  } else if ("invoice_date" in editedRowData) {
    updateInvoiceComment(editedRowData);
  } else if ("shipper" in editedRowData) {
    updateLaserShipmentKpiComment(editedRowData);
  }
}

function updateLaserShipmentKpiComment(editedRowData) {
  fetch(`${url}/comments/Laser/shipment`, {
    method: "PUT",
    credentials: "include",
    headers: {
      "Content-Type": "application/json;charset=utf-8",
    },
    body: JSON.stringify(editedRowData),
  });
}

function updateShipmentComment(editedRowData) {
  fetch(`${url}/comments/IntermediateDB/shipment`, {
    method: "PUT",
    credentials: "include",
    headers: {
      "Content-Type": "application/json;charset=utf-8",
    },
    body: JSON.stringify(editedRowData),
  });
}

function updateInvoiceComment(editedRowData) {
  fetch(`${url}/comments/Laser/invoice`, {
    method: "PUT",
    credentials: "include",
    headers: {
      "Content-Type": "application/json;charset=utf-8",
    },
    body: JSON.stringify(editedRowData),
  });
}

function toggleList(isOpen, toggleButtonContainer, cell) {
  // Retrieve the additional info section for the associated row
  const collapse = cell
    .getRow()
    .getElement()
    .getElementsByClassName("tabulator-responsive-collapse")[0];

  let open = isOpen;
  // If the state is open, ensure to display the button with the class open and display the additional columns for that row
  // else ensure to display the button without the open class and hide the additional columns for that row
  if (open) {
    toggleButtonContainer.classList.add("open");
    if (collapse) {
      collapse.style.display = "";
    }
  } else {
    toggleButtonContainer.classList.remove("open");
    if (collapse) {
      collapse.style.display = "none";
    }
  }
  return open;
}

function customResponsiveCollapseFormatter(cell, formatterParams, onRendered) {
  let open = false; // Initial state of the additional information section
  // Create the element to contain the two buttons which will be swapped according to the state of the additional fields.
  const toggleButtonContainer = document.createElement("div");
  // Specify that the element will contain our toggle buttons by adding the appropriate class used by tabulator
  toggleButtonContainer.classList.add("tabulator-responsive-collapse-toggle");
  // Define the toggle buttons to be contained within the column
  toggleButtonContainer.innerHTML = `
    <button class='tabulator-responsive-collapse-toggle-open'>
      Expand
    </button>
    <button class='tabulator-responsive-collapse-toggle-close'>
      Collapse
    </button>`;

  cell.getElement().classList.add("tabulator-row-handle"); // Adds the default styles to the value of the cell

  // If the table property defines the additional columns to be displayed on page load, change the state accordingly
  if (this.table.options.responsiveLayoutCollapseStartOpen) {
    open = true;
  }

  // add event listener to toggle the additional columns display and to change to the appropriate button
  // Update the open state after the method has executed
  toggleButtonContainer.addEventListener("click", function () {
    open = toggleList(!open, toggleButtonContainer, cell);
  });

  // Initialise the table view based on default value
  open = toggleList(open, toggleButtonContainer, cell);

  // Return the customised element containing the toggle buttons to be displayed within the column
  return toggleButtonContainer;
}

function linkOrderFormatter(cell, formatterParams, onRendered) {
  let open = false; // Initial state of the additional information section
  // Create the element to contain the two buttons which will be swapped according to the state of the additional fields.

  const actionsContainer = document.createElement("div");
  cell.getElement().classList.add("tabulator-row-handle");
  actionsContainer.classList.add("tabulator-responsive-collapse-toggle");

  // Specify that the element will contain our toggle buttons by adding the appropriate class used by tabulator

  // Adds the default styles to the value of the cell
  let action = "create-order";
  if (this.selectedCountrySpecification.update_order_button) {
    action = "update-order";
  }
  let labels = {
    'create-order': "Create",
    'update-order': "Update"
  }

  // add event listener to toggle the additional columns display and to change to the appropriate button
  // Update the open state after the method has executed
  const callConfirm = this.openModal.bind(this, cell.getData(), actionsContainer, action, this.$refs.LinkModal);
  // Create the "Link Order" button
  const linkOrderButton = document.createElement("button");
  linkOrderButton.textContent = "Link";
  linkOrderButton.addEventListener("click", callConfirm);

  // Create the second button and its associated function
  const serverActionButton = document.createElement("button");
  serverActionButton.textContent = labels[action];
  serverActionButton.addEventListener("click", () => {
    this.serverAction({ data: cell.getData(), element: actionsContainer, action: action })
  });

  let buttons = [linkOrderButton, serverActionButton]
  buttons.forEach(button => {
    button.classList.add('mx-1')
  })

  // Append the buttons to the actionsContainer
  actionsContainer.appendChild(linkOrderButton);
  actionsContainer.appendChild(serverActionButton);
  // Initialise the table view based on default value
  // Return the customised element containing the toggle buttons to be displayed within the column
  return actionsContainer;
}

function actionFormatter(cell, formatterParams, onRendered) {
  // Dropdown configuration
  const isUp = true; // Whether it's an "up" dropdown
  const triggerClass = "dropdown-trigger";
  const menuClass = "dropdown-menu";
  const items = [
    "Item 1",
    "Item 2",
    "Item 3"
  ];

  // Create the dropdown shell
  const dropdown = document.createElement("div");
  dropdown.classList.add("dropdown");

  // Create the dropdown trigger button
  const trigger = document.createElement("div");
  trigger.classList.add(triggerClass);

  const button = document.createElement("button");
  button.classList.add("button");
  button.setAttribute("aria-haspopup", "true");
  button.setAttribute("aria-controls", "dropdown-menu");

  const buttonText = document.createElement("span");
  buttonText.textContent = "Dropup button";

  const icon = document.createElement("span");
  icon.classList.add("icon", "is-small");
  icon.innerHTML = '<i class="fas fa-angle-up" aria-hidden="true"></i>';

  button.appendChild(buttonText);
  button.appendChild(icon);
  trigger.appendChild(button);

  // Create the dropdown menu and content
  const menu = document.createElement("div");
  menu.classList.add(menuClass);
  menu.setAttribute("role", "menu");

  const content = document.createElement("div");
  content.classList.add("dropdown-content");

  button.addEventListener("click", () => {
    dropdown.classList.toggle("is-active");
  });

  // Create and add dropdown items dynamically
  for (const itemText of items) {
    const item = document.createElement("div");
    item.classList.add("dropdown-item");
    item.textContent = itemText;
    content.appendChild(item);
  }

  menu.appendChild(content);

  // Append everything to the dropdown shell
  dropdown.appendChild(trigger);
  dropdown.appendChild(menu);

  // Append the dropdown to the HTML



  let open = false; // Initial state of the additional information section
  // Create the element to contain the two buttons which will be swapped according to the state of the additional fields.

  const actionsContainer = document.createElement("div");
  cell.getElement().classList.add("tabulator-row-handle");
  actionsContainer.classList.add("tabulator-responsive-collapse-toggle");

  // Specify that the element will contain our toggle buttons by adding the appropriate class used by tabulator

  // Adds the default styles to the value of the cell
  let action = "create-order";
  if (this.selectedCountrySpecification.update_order_button) {
    action = "update-order";
  }
  let labels = {
    'create-order': "Create",
    'update-order': "Update"
  }

  // add event listener to toggle the additional columns display and to change to the appropriate button
  // Update the open state after the method has executed
  const callConfirm = this.openModal.bind(this, cell.getData(), actionsContainer, action, this.$refs.LinkModal);
  // Create the "Link Order" button
  const linkOrderButton = document.createElement("button");
  linkOrderButton.textContent = "Link";
  linkOrderButton.addEventListener("click", callConfirm);

  // Create the second button and its associated function
  const serverActionButton = document.createElement("button");
  serverActionButton.textContent = labels[action];
  serverActionButton.addEventListener("click", () => {
    this.serverAction({ data: cell.getData(), element: actionsContainer, action: action })
  });

  let buttons = [linkOrderButton, serverActionButton]
  buttons.forEach(button => {
    button.classList.add('mx-1')
  })

  // Append the buttons to the actionsContainer
  actionsContainer.appendChild(linkOrderButton);
  actionsContainer.appendChild(serverActionButton);
  // Initialise the table view based on default value
  // Return the customised element containing the toggle buttons to be displayed within the column
  return dropdown;
}




async function serverAction(payload) {
  let data = payload.data
  let linkOrderButtonContainer = payload.element
  const id = data.id;
  const number = data.number;

  const formattedUrl = `${url}/${payload.action}`;
  let button = this.link_button
  if (payload.action == "update-action") {
    button = this.update_pro_button
  }

  this.$refs.notification.addNotification(
    `Processing`,
    'is-info',
    2000,
    []
  );

  try {
    const response = await fetch(formattedUrl, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        id: id,
      }),
    });

    const responseBody = await response.json();

    if (response.ok) {
      this.removeReceipt(data)
      const actions = [
        {
          label: 'Go to',
          handler: (notification) => {
            const newWindow = window.open(responseBody.order_url, '_blank');
            newWindow.focus();
          },
        },
      ];

      this.$refs.notification.addNotification(
        `Linked ORD for: ${number}`,
        'is-success',
        5000,
        actions
      );
    } else {
      this.$refs.notification.addNotification(
        `Failed ${responseBody.message}`,
        'is-danger',
        5000,
        []
      );
    }
  } catch (error) {
    console.log(error)
    this.$refs.notification.addNotification(
      `An error occurred.`,
      'is-danger',
      5000,
      []
    );
  }

}

function removeReceipt(warehouseReceipt) {
  var rows = this.table.searchRows("number", "=", warehouseReceipt.number);
  rows.forEach(row => {
    row.delete()
  })
}



function openModal(data, element, action, modal) {
  // this.$refs.confirmationModal.openModal(data, element, action);
  modal.openModal(data, element, action);

}




function getRecordAttachmentsFormatter(cell) {
  cell.getElement().classList.add("tabulator-row-handle"); // Adds the default styles to the value of the cell
  return `
  <div class="tabulator-responsive-collapse-toggle">
    <button class="attachment-button">
      View
    </button>
  </div>`;
}

function viewRecordAttachments(event, cell) {
  this.modalRecord = cell.getRow().getData();
  this.toggleModal();
}
function toggleModal() {
  this.activateModal = !this.activateModal;
}
</script>

