<template>
    <div class="action-buttons">
        <button v-for="action in actions" :key="action.label" class="action-button" @click="handleAction(action)">
            {{ action.label }}
        </button>
    </div>
</template>
  
<script>
export default {
    props: ['actions'],
    methods: {
        handleAction(action) {
            if (action.handler && typeof action.handler === 'function') {
                action.handler();
            }
            if (action.emit && typeof action.emit === 'string') {
                this.$emit(action.emit);
            }
            this.$emit('action-clicked', action);
        },
    },
};
</script>
  

  
<style scoped>
.action-buttons {
    margin-top: 0.625rem;
}

.action-button {
    margin-right: 0.625rem;
    background-color: transparent;
    border: none;
    color: inherit;
    cursor: pointer;
    outline: none;
}
</style>
  