<template>
  <div class="notification-container">
    <div
      v-for="notification in notifications"
      :key="notification.id"
      class="notification"
      :class="notification.type"
    >
      <button class="delete" @click="removeNotification(notification.id)"></button>
      <div class="notification-content">
        <div class="notification-message" v-html="notification.message"></div>
        <action-buttons :actions="notification.actions"></action-buttons>
      </div>
    </div>
  </div>
</template>

<script>
import ActionButtons from './ActionButtons.vue';

export default {
  name: 'Notification',
  components: {
    ActionButtons,
  },
  data() {
    return {
      notifications: [],
      nextNotificationId: 1,
    };
  },
  methods: {
    addNotification(message, type, duration, actions) {
      const notification = {
        id: this.nextNotificationId++,
        message,
        type,
        actions,
      };

      this.notifications.push(notification);

      if (duration > 0) {
        setTimeout(() => {
          this.removeNotification(notification.id);
        }, duration);
      }
    },
    removeNotification(id) {
      this.notifications = this.notifications.filter((notification) => notification.id !== id);
    },
  },
};
</script>



  
  <style scoped>
  .notification-container {
    position: fixed;
    top: 0.625rem;
    right: 0.625rem;
    z-index: 9999;
  }
  
  .notification {
    position: relative;
    margin-bottom: 0.625rem;
    color: #fff;
  }
  
  .notification-content {
    padding: 0.625rem;
  }
  
  .notification-message {
    margin-bottom: 0.625rem;
  }
  
  
  </style>
  