<template>
    <div>
        <Search @search="performSearch"></Search>
        <div ref="table"></div>
    </div>
</template>
  
<script>
import { TabulatorFull as Tabulator } from "tabulator-tables";
import Search from "./Search.vue"
export default {
    components: {
        Search,
    },
    props: {
        tableDefinition: {
            type: Array,
            required: true
        },
        tableData: {
            type: Array,
            required: true
        }
    },
    mounted() {
        this.initializeTable();
    },
    methods: {
        initializeTable() {
            this.table = new Tabulator(this.$refs.table, {
                columns: this.tableDefinition,
                data: this.tableData,
                selectable: true,
                pagination: "local", //paginate the data
                paginationSize: 5, //allow 7 rows per page of data
                paginationCounter: "rows", //display count of paginated rows in footer
                // Additional configuration options for Tabulator
            });
            // Set listeners for rowSelected and rowDeselected events
            this.table.on("rowSelected", this.handleRowSelection);
            this.table.on("rowDeselected", this.handleRowDeselection);
        },
        performSearch(searchInput) {
            const filters = this.tableDefinition.map(column => {
                return { field: column.field, type: 'like', value: searchInput };
            });
            this.table.setFilter([filters]);
        },
        handleRowSelection(row) {
            console.log("row-selected")
            this.$emit('row-selected', { row: row }); // Emit selected order data
        },
        handleRowDeselection(row) {

            this.$emit('row-deselected', { row: row }); // Emit deselected order event
        },
    }

}
</script>
  
