<template>
    <div v-show="isOpen" class="modal is-active">
        <div class="modal-background"></div>
        <div class="modal-card">
            <header class="modal-card-head">
                <p v-if="receiptData" class="modal-card-title">Confirm action for PRO: {{ receiptData.number }}?</p>
                <button class="delete" aria-label="close" @click="cancel"></button>
            </header>
            <section class="modal-card-body" v-if="receiptData">
                <p>Warehouse Receipt Number: {{ receiptData.number }}</p>
                <p>Date Delivered to Warehouse: {{ receiptData.created_date }}</p>
                <p v-show="receiptData.po_number">Purchase Order Number: {{ receiptData.po_number }}</p>
                <p v-show="receiptData.invoice">Supplier Invoice: {{ receiptData.invoice }}</p>
                <p>Supplier: {{ receiptData.shipper }}</p>
                <p>Consignee: {{ receiptData.consignee }}</p>
                <p>Direction: Import</p>
                <p>Trucker Company: Supplier</p>
            </section>
            <footer class="modal-card-foot">
                <button class="button is-success" @click="confirm">Continue</button>
                <button class="button" @click="cancel">Cancel</button>
            </footer>
        </div>
    </div>
</template>
  
<script>
export default {
    name: 'ProConfirmationModal',
    props: {
        warehouseReceipt: {
            type: String,
            required: true,
        },
    },
    data() {
        return {
            isOpen: false,
            receiptData: null,
            element: null,
            action: ""
        };
    },
    methods: {
        openModal(receiptData, element, action) {
            this.isOpen = true;
            this.receiptData = receiptData
            this.element = element
            this.action = action
            console.log(receiptData, this.isOpen)
        },
        closeModal() {
            this.isOpen = false;
            this.receiptData = null;
            this.element = null
            this.action = ""

        },
        confirm() {
            this.$emit("confirm", { data: this.receiptData, element: this.element, action: this.action });
            this.closeModal();
        },
        cancel() {
            this.$emit('cancel');
            this.closeModal();
        },
    },
};
</script>
  


  
<style scoped>


.modal-card-body {
    color: black;
}
</style>