<template>
    <div class="control has-icons-right">
        <input class="input is-small" type="search" v-model="searchInput" placeholder="Search..." @input="filterData">
        <span class="icon is-small is-right">
            <font-awesome-icon icon="fa-solid fa-search" />
        </span>
    </div>
</template>
  
<script>
export default {
    data() {
        return {
            searchInput: ''
        }
    },
    methods: {
        filterData() {
            // Emit an event to the parent component with the search input value
            this.$emit('search', this.searchInput);
        }
    }
}
</script>
  
