<template>
  <Modal :title="modalTitle" :is-active="isActive" @close="closeModal">
    <template #default>
      <Table :table-definition="tableDefinition" :table-data="tableData" @row-selected="handleRowSelected"
        @row-deselected="handleRowDeselected" ref="Table"></Table>
    </template>
    <template #footer>
      <button class="button" :disabled="!selectedRows.length" @click="performAction">Link</button>
      <button class="button" @click="emitConfirmation" :disabled="selectedRows.length">{{ actionLabels[action] }}</button>

    </template>
  </Modal>
</template>

<script>
import Modal from './Modal.vue';
import Table from './Table.vue';
import { url } from "../App.vue";

export default {
  name: 'LinkModal',
  components: {
    Modal,
    Table
  },
  props: {
    warehouseReceipt: {
      type: Object,
      required: true
    }
  },

  data() {
    return {
      modalTitle: 'Link Modal',
      selectedRows: [],
      isActive: false,
      warehouseReceipt: undefined,
      tableDefinition: [
        {
          title: 'ORD #',
          field: 'pro_ref_num',
          visible: true,
          responsive: 4,
          compute: false,
          vertAlign: 'middle',
          selectable: true
        },
        {
          title: 'Consignee',
          field: 'pro_consignee',
          visible: true,
          responsive: 5,
          compute: false,
          vertAlign: 'middle',
          selectable: true
        },
        {
          title: 'Supplier',
          field: 'pro_shipper',
          visible: true,
          responsive: 5,
          compute: false,
          vertAlign: 'middle',
          selectable: true
        },
        {
          title: 'PO #',
          field: 'pro_po_number',
          visible: true,
          responsive: 2,
          compute: false,
          vertAlign: 'middle',
          width: 300,
          selectable: true
        },
        {
          title: 'Supplier Promise Date',
          field: 'supplier_promise_date',
          visible: true,
          responsive: 5,
          compute: false,
          vertAlign: 'middle',
          selectable: true
        },
        {
          title: 'Mode Of Transport',
          field: 'pro_mode_of_transport',
          visible: true,
          responsive: 2,
          compute: false,
          vertAlign: 'middle',
          selectable: true
        },
      ],
      tableData: [
        {
          pro_mode_of_transport: 'Transport 1',
          pro_shipper: 'Supplier 1',
          pro_po_number: 'PO001',
          pro_ref_num: 'ORD001',
          supplier_promise_date: '2023-07-10'
        },
        {
          pro_mode_of_transport: 'Transport 2',
          pro_shipper: 'Supplier 2',
          pro_po_number: 'PO002',
          pro_ref_num: 'ORD002',
          supplier_promise_date: '2023-07-12'
        },
        {
          pro_mode_of_transport: 'Transport 3',
          pro_shipper: 'Supplier 3',
          pro_po_number: 'PO003',
          pro_ref_num: 'ORD003',
          supplier_promise_date: '2023-07-15'
        }
      ],
      actionLabels: {
        "create-order": "Create",
        "update-order": "Update"
      },
      action: ""
    }
  },
  async beforeMount() {
    // Ensure that the user's changes persist on change to another component/page such as the summary page
    this.tableData = await this.getOrders();
  },
  methods: {
    async openModal(warehouseReceipt, element, action) {
      this.warehouseReceipt = warehouseReceipt
      this.action = action
      // Retrieve the response body and assign the response json data to the appropriate component property
      // Then initialise the table with the retrieved data

      this.tableData = await this.getOrders();
      this.$refs.Table.initializeTable()
      this.isActive = true;

    },
    async getOrders() {
      let dataEndpoint = "laser-orders"
      let formattedUrl = `${url}/${dataEndpoint}`
      // Assign the warehouseReceipt to a component data property or use it directly in your logic
      const response = await fetch(formattedUrl, {
        credentials: "include", // Ensure that the credentials cookie is stored and issued with cross origin requests.
        method: "GET", // POST, PUT, DELETE, etc.
      });
      if (response.ok) {
        const responseBody = await response.json();
        return responseBody
      } else {
        return []
      }

    },
    closeModal() {
      this.isActive = false;
      this.selectedRows = []
      this.action = ""
    },
    handleRowSelected(payload) {
      // Deselect all previously selected rows
      console.log(payload)
      this.selectedRows.forEach((selectedRow) => {
        selectedRow.deselect();
        selectedRow.getElement().classList.remove("selected-row");
      });
      const row = payload.row
      // Select the new row
      row.getElement().classList.add("selected-row");

      // Update the selectedRows array
      this.selectedRows = [row];
    },
    handleRowDeselected(payload) {
      // Deselect the deselected row
      const row = payload.row
      row.getElement().classList.remove("selected-row");

      // Remove the deselected row from the selectedRows array
      this.selectedRows = []
    },
    async performAction() {
      // Perform the action using the selected rows (this.selectedRows)
      if (this.selectedRows.length > 0) {
        console.log("Selected Rows:", this.selectedRows);
        let order = this.selectedRows[0].getData()
        let payload = { receipt: this.warehouseReceipt, order: order }
        console.log(JSON.stringify(payload))
        const response = await fetch(`${url}/update-receipt-by-order`, {
          headers: {
            "Content-Type": "application/json",
          },
          credentials: "include", // Ensure that the credentials cookie is included within cross-origin requests
          method: "POST", // POST, PUT, DELETE, etc.
          body: JSON.stringify(payload),
        });

        if (response.ok) {
          const responseBody = await response.json();
          console.log(responseBody, "res")
          this.$emit("removeReceipt", this.warehouseReceipt);
          this.$emit("addNotification", { message: `Linked ${order.pro_ref_num} to ${this.warehouseReceipt.number}`, type: "is-success", duration: 5000, actions: [] });

          this.closeModal();

        }


      }
    },
    emitConfirmation() {
      this.$emit("confirmationModal", { data: this.warehouseReceipt, element: null, action: this.action });
      this.closeModal();

    }
  },
}
</script>

